import React from 'react';
import InputField from '../form_components/InputField'
import WorkerListItem from './WorkerListItem'
class WorkersList extends React.Component {
  constructor(props) {
    super(props);
    this.state = { searchKey: "" };
  }

  workersList() {
    const { selectedWorker } = this.props
    return this.props.workers.map((worker, index) => {
      const isSelected = (worker.id == selectedWorker.id)
      return (
        <CSSTransition
              in={worker}
              timeout={300}
              classNames="message"
              unmountOnExit>
        <WorkerListItem isSelected={isSelected} worker={worker} key={index} selectWorker={this.props.selectWorker}/>
        </CSSTransition>
      )
    })
  }

  render() {
    return (
      <ul className="workers-list list-unstyled">
        {this.workersList()}
      </ul>
    );
  }
}


export default WorkersList;
